              console.log(res);
              if(res.data.ret == 200) {
                console.log();
                this.$message.success("上传成功");
                this.target = false;
                this.rulefm.name = "";
                this.rulefm.desc = "";
                this.getData();
              } else {
                this.$message.error("上传失败");
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
