//    重置输入框
      resetForm() {
        //      this.$refs[formName].resetField();
        this.rulefm.name = "";
        this.rulefm.desc = "";
      },
      //上传语音
      submitUpload() {
        this.$refs.upload.submit();
      },
      //创建目标人时上传语音
      submitUploadone() {
        if(this.newfilemid !=""){
          
          this.$refs.uploadone.submit();
        }else{
          this.$message.error("请先创建目标人");
        }
      },
//    上传成功时的钩子函数
      handleSuccess(response, file, fileList){
        console.log(fileList);
        this.i+=1;
        if(fileList.length == this.i){
          this.$message.success("上传成功");
          this.xulian=false;
          fileList.splice(0,fileList.length);
          this.xl();
          this.i = 0;
        }
      },
//    上传文件失败时的钩子函数
      handleError(){
        this.i = 0;
      },
//    创建目标人时上传成功时的钩子函数
      handleSuccessone(response, file, fileList){
        console.log(fileList);
        this.i+=1;
        if(fileList.length == this.i){
          this.$message.success("上传成功");
          fileList.splice(0,fileList.length);
          this.xulian=false;
          this.xlone();
          this.i = 0;
        }
      },
//    创建目标人时上传文件失败时的钩子函数
      handleErrorone(){
        this.i = 0;
      },
//    详情语音列表播放按钮
      play(scope){
        console.log(scope);
      },
//    详情语音列表删除按钮
      speechDel(tsid){
        console.log(tsid);
        speechDel(tsid).then((res)=>{
          console.log(res);
          if(res.data.ret == 200){
            this.$message.success("删除成功");
            this.xl();
            this.xlone();
          }
        })
      },
      //文件列表移除文件时的钩子
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      //点击文件列表中已上传的文件时的钩子
      handlePreview(file) {
        console.log(file);
      },
//    上传头像
      fundata(){
        var formData = new FormData();
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
//    判断上传的头像大小
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    },
    data() {
      const data = [{
        id: 1,
        label: '一级 1'
      }, {
        id: 2,
        label: '一级 2'
      }, {
        id: 3,
        label: '一级 3'
      }];
      return {
        Image:false,
        https:"http://192.168.1.118/sr/model/speech/add",
        fenye: false,
        thisi:0,
//      imgshi:'/static/img/'+ this.imageUrl +'.jpg',
//      上传头像
//      thisi:1,
        imageUrl: '',
//      xiug:true,
        //当前页码
        currentPage: 1,
        //默认每页数据量
