        //默认数据总数
        totalCount: 0,
        //页码数据
        i:0,
        bt:'',
        name: '',
        age: '',
        file: '',
        filetwo: '',
        filemid:'',
        fileList: [],
        fileListone: [],
        speech:[],
        speechone:[],
        crew:[],
        data: [],
        detail:{},
        selectItem:-2,
        xulian:true,
        kong:true,
        xunlian:false,
        xunlianone:false,
        target: false,
        addMan: false,
        details: false,
        targetdatas: false,
        alter:false,
        mId:'',
        gId:"",
        iGid:"",
        buer: false,
        data4: JSON.parse(JSON.stringify(data)),
        data5: JSON.parse(JSON.stringify(data)),
        formInline: {
          speaker:"",
          idcard:"",
          birth:"",
          gender:"",
          nation:"",
          room:"",
          address:"",
          gid:""
        },

        filterText: '',
        ruleForm: {
          name: '',
          date1: '',
          sex: ''
        },
        addrulefm: {
          name: '',
          gid:'',
          idcard: '',
          birth: '',
          gender: '',
          nation: '',
          room: '',
          tel: '',
          address: '',
          desc:'',
          pic:''
        },
        ImgUrl:'',
        reader:'',
        readertwo:'',
        add_rules_target: {
          name: [{
              required: true,
              message: '请输入姓名',
              trigger: 'blur'
            },
            {
              min: 2,
              max: 15,
              message: '长度在 2 到 15 个字符',
              trigger: 'blur'
            }
          ]
        },
        rulefm: {
          id: 0,
          name: '',
          desc: ''
        },
        rulefm_one: {
          id: 0,
          name: '',
          desc: ''
        },
        rules_target: {
          name: [{
              required: true,
              message: '请输入目标组名称',
              trigger: 'blur'
            },
            {
              min: 1,
              max: 15,
              message: '长度在 1 到 15 个字符',
              trigger: 'blur'
            }
          ]
        },
        rules: {
          name: [{
              required: true,
              message: '请输入活动名称',
              trigger: 'blur'
            },
            {
              min: 3,
              max: 5,
              message: '长度在 3 到 5 个字符',
              trigger: 'blur'
            }
          ]
        },
//      新建模型上传语音需要的mid，点击关闭清空
        newfilemid:"",
//      模型组展示
        data2: [],
        http:'http://192.168.1.118'
      }
    }
  }
</script>


